import React from "react"
import { Link } from "gatsby"
import Layout from "../components/Layout"

const labels = {
  join_to_event_header: `Join to an event`,
  create_new_event_header: `Create a new event`,
  create_new_event_text: `Lets create a new event and share your friends`,
}

const InsertEventCode = () => {
  return (
    <form action="" className="flex-col border-container">
      <h6>{labels.join_to_event_header}</h6>
      <input placeholder="Enter event code" type="text" />
      <input type="submit" value="Enter"></input>
    </form>
  )
}

const CreateNewEvent = () => {
  return (
    <form className="flex-col border-container">
      <h6>{labels.create_new_event_header}</h6>
      <p>{labels.create_new_event_text}</p>
      <button>
        <Link to="/create-event/">Create event</Link>
      </button>
    </form>
  )
}

export const IndexPage = () => (
  <>
    <h2>Hi people</h2>
    <InsertEventCode />
    <CreateNewEvent />
  </>
)

const LayoutIndexPage = () => (
  <Layout>
    <IndexPage />
  </Layout>
)

export default LayoutIndexPage
